.Button {
    cursor: pointer;
    display: inline-block;
    border-radius: 8px;
    padding: 8px 18px;
    background: var(--dark);

    span {
        font-size: 12px;
        font-weight: 400;
        text-transform: uppercase;
        background: var(--gradient_text);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        filter: brightness(110%);
    }
}